import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpProvider } from '../../providers/http/http';
import { UtilsProvider } from '../../providers/utils/utils';
import { Storage } from '@ionic/storage';

/**
 * Generated class for the ProductToDebitPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-product-to-debit',
  templateUrl: 'product-to-debit.html',
})
export class ProductToDebitPage {

  public cuentasPropias: string;
  public bandera:string;
  public seleccion: string;
  private PAY_BILL:string ='payBill';
  private CREDIT_CARD:string ='creditCard';

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, public utils: UtilsProvider) {

    

    //obtencion de la bandera   
    this.bandera =  this.navParams.get("flag"); 

    
    if(this.bandera==this.PAY_BILL){      
      //obtenemos listas cargadas en bills-to-pay
      this.storage.get('misCuentas').then((valor) => {        
        this.cuentasPropias = valor;
  
      });
      //alert("Entro a pagos de pay bills**");
    }
    /*
       else if(this.bandera==this.CREDIT_CARD){
      //ObtenciÃ³n de mis cuentas -> variable obtenida del ProductOverView
      this.storage.get('accountLst').then((valor) => {
        this.cuentasPropias = valor;
      });
      alert("Entro a pagos de tarjeta2");
        
    }*/

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProductToDebitPage');
  }

  //Metodo de la cuenta que selecciono
  /**
   * 
   * @param pos ->posicion del arreglo obj seleccionado
   * @param info->info account 
   * @param cuenta->reg full in pos 
   */
  cuentaSeleccionada(pos, info, cuenta){
    //alert(cuenta+" <-> \n "+info+" <-> \n "+pos);
   if(this.bandera == this.PAY_BILL){
      this.seleccion = cuenta
      //alert("seleccion->"+this.seleccion);
      this.storage.set('accountSelectedTopay', this.seleccion);
      this.storage.set('infoCuenta2', info);
      //alert("info ->"+info);
      
      this.storage.set('posElementoPayBills', pos);
      this.navCtrl.push("ProductToPayBillPage",{ flagCuentaAsociada: 2 });
    }
    /*else if(this.bandera==this.CREDIT_CARD){
        this.seleccion = cuenta
        //alert("seleccion->"+this.seleccion);
        this.storage.set('accountSelectedTopay', this.seleccion);
        this.navCtrl.push("ProductToPayPage");
    }*/

  }

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }

}



